import React, {useState} from 'react'
import styled from '@emotion/styled';
import PropType from 'prop-types';
import { obtenerDiferenciaYear, calcularMarca, obtenerPlan } from '../helpers';
const Campo = styled.div`
    display: flex;
    margin-bottom: 1rem;
    align-items: center;
`;
const Label = styled.label`
    flex: 0 0 100px;
`;
const Select = styled.select`
    display: block;
    width: 100%;
    padding: 1rem;
    border: 1px solid #e1e1e1;
    -webkit-appearance: none;
`;
const InputRadio = styled.input`
    margin: 0 1rem;
`;
const Boton = styled.button`
    background-color: #00838f;;
    font-size: 16px;
    width: 100%;
    padding:1rem;
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    border: none;
    transition: background-color .3s ease;
    margin-top: 2rem;
    &:hover{
        cursor: pointer;
        background-color: #26c6da;
    }
`;
const Error = styled.div`
    background-color: red;
    color: white;
    padding: 1rem;
    width: 100%;
    text-align: center ;
    margin-bottom: 2rem;
`;
function Formulario({guardarResumen, guardarCargando}) {

    const [datos, guardarDatos] = useState({
        marca:'',
        year:'',
        plan:'',
    });
    
    const [error, guardarError] = useState(false);
    //extraer los valores del state
    const {marca, year, plan} = datos;

    //Leer los datos del formulario y colocarlos en el state
    const obtenerInformacion = e => {
        guardarDatos({
            ...datos,
            [e.target.name] : e.target.value
        });
    }

    //Cuando el usuario presiona submit
    const cotizarSeguro = e => {
        e.preventDefault();
        if (marca.trim() === '' || year.trim() === '' || plan.trim() === '') {
            guardarError(true);
            return;
        }
        guardarError(false);

        //una base de 2000

        let resultado = 2000
        //Obtener la diferencia de años
        const diferencia = obtenerDiferenciaYear(year);
        
        //Por cada año hay que restar el 3%
        resultado -= ((diferencia * 3) * resultado) / 100;
        
        //Americano 15%
        //Asiatico 5%
        //Europeo 30%
        resultado = calcularMarca(marca) * resultado;
        
        //Basico aumenta 20%
        //Completo 50%
        const incrementoPlan = obtenerPlan(plan);
        resultado = parseFloat(incrementoPlan * resultado).toFixed(2);
        guardarCargando(true);
        //Total
        setTimeout(() => {
            //Elimina el spinner
            guardarCargando(false);

            //pasa la información al componente principal
            guardarResumen({
                cotizacion: Number(resultado),
                datos
            })
        }, 3000);
    }
    return (
        <form
            onSubmit={cotizarSeguro}
        >
            {error ? <Error>Todos los campos son obligatorios</Error> : null}
            <Campo>
                <Label>Marca</Label>
                <Select name="marca" value={marca} onChange={obtenerInformacion}>
                    <option value="">-- Seleccione --</option>
                    <option value="americano">Americano</option>
                    <option value="europeo">Europeo</option>
                    <option value="asiatico">Asiatico</option>
                </Select>
            </Campo>
            <Campo>
                <Label>Año</Label>
                <Select name="year" value={year} onChange={obtenerInformacion}>
                    <option value="">-- Seleccione --</option>
                    {[...(Array(10).keys())].map((i) => (
                        <option value={(new Date()).getFullYear() - i}>{(new Date()).getFullYear() - i}</option>

                    ))}
                </Select>
            </Campo>
            <Campo>
                <Label>Plan</Label>
                <InputRadio 
                    type="radio"
                    name="plan"
                    value="basico"
                    checked={plan === "basico"}
                    onChange={obtenerInformacion}
                />Básico
                <InputRadio 
                    type="radio"
                    name="plan"
                    value="completo"
                    checked={plan === "completo"}
                    onChange={obtenerInformacion}
                />Completo
            </Campo>

            <Boton type="submit">Cotizar</Boton>
        </form>
    )
}
Formulario.propType = {
    guardarResumen: PropType.func.isRequired,
    guardarCargando: PropType.func.isRequired
}
export default Formulario
